.skills{
    width: 100vw;
    height: 80vh;
    margin-top: 250px;
    h2{
        text-align: center;
        margin-bottom: 100px ;
    }
    .skills_container{
        display: grid;
        grid-template-columns: repeat(4, 100px);
        grid-template-rows: repeat(2, 100px);
        gap: 30px;
        max-width: 500px;
        margin: 0 auto;
    }
    .skills_block{
     
        width: 100px;
        height: 100px;
        margin: 0 auto;
    }
    img{
        width: 100%;
        height: 100%;
    }
}
@media (max-width: 702px){
    .skills_container{
        display: grid;
        grid-template-columns: repeat(2, 100px) !important;
        grid-template-rows: repeat(4, 100px) !important;
        width: 210px !important ;
    }
}
@media (max-width: 390px){
    .skills_container{
        margin-left: 40px !important;
    }
}