.aboutSlide{
    width: 100vw;
    min-height: 100vh;
    overflow-y: hidden;
    .face-block{
        padding-top: 150px;
    }
    .face{
        width: 250px;
        height: 250px;
        border-radius: 50%;
        border: 5px solid white;
        margin: 0 auto;
        margin-bottom: 40px;
    }
    .welcome__title{
        font-size: 50px;
        text-align: center;
        margin-bottom: 50px;
    }
    .aboutMe{
        width: 150px;
        height: 35px;
        background-color: blanchedalmond;
        box-shadow: 3px 3px 10px #804141;
        margin: 0 auto;
        text-align: center;
        font-size: 30px;
        border-radius: 30px;
        cursor: pointer;
        &:hover{
            box-shadow: 0px 0px 5px #804141;
        }
    }
    .social{
        width: 150px;
        margin: 0 auto;
        margin-top: 30px;
        display: flex;
        gap: 2px    ;
        padding-bottom: 100px;
        .social-tag{
            width: 50px;
            height: 50px;
            gap: 5px;
        }
        .gh{
            width: 60px;
            height: 60px;
            margin-bottom: 10px;
            position: relative;
            bottom: 5px;
            right: 5px;
        }
    }
    .face__photo{
        width: 100%;
        height: 100%;
        border-radius: 50%;
    }
}