.JavaScriptProject{
    margin: 0 auto;
    width: 100vw;
    height: 100vh;
    text-align: center;
    h1{
        margin-top: 50px;
        margin-bottom: 100px;
    }
    .Project-box{
        width: 960px;
        margin: o auto;
        display: grid;
        margin: 0 auto;
        gap: 30px;
        grid-template-columns: repeat(3, 300px);
        grid-template-rows: repeat(2, 300px);
        .project-item{
            border: 1px  black solid;
            position: relative;
            &:hover  .project-description{
                visibility: visible;
                overflow: hidden;
                animation: shadow 0.7s ease;
            }
        }
    }
    .project__link{
        text-decoration: none;
        color: black;
    }
}