.WebsiteProject, .JavaScriptProject{
    margin: 0 auto;
    width: 100vw;
    text-align: center;
    height: 90vh;
    h1{
        margin-top: 50px;
        margin-bottom: 100px;
    }
    .Project-box{
        width: 960px;
        margin: o auto;
        display: grid;
        margin: 0 auto;
        gap: 30px;
        grid-template-columns: repeat(4, 300px);
        grid-template-rows: repeat(2, 300px);
        .project-item{
            border: 1px  black solid;
            position: relative;
            &:hover  .project-description{
                visibility: visible;
                overflow: hidden;
                animation: shadow 0.7s ease;
            }
        }
    }
    .project__link{
        text-decoration: none;
        color: black;
    }
    .project-btn{
        padding: 5px 10px;
        border-radius: 10px;
        border: 1px black solid;
        cursor: pointer;
    }
    .project-description{
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
       background-color: gray;
       font-size: 23px;
       visibility: hidden;
       span{
        font-weight: bold;
       }
    }
    &hover  .project-description{
       
        animation: shadow 1s ease;
        animation-iteration-count: 1;
    }
}
@keyframes shadow{
    0%{
        
        height: 0%;
    }
    100%{
        height: 100%;
    }
}