.shadow-block{
    width: 100vw;
    min-height: 100vh;
    max-height: 1000vh;
    background-color: rgba(0, 0, 0, 0.3);
    position: fixed;
    top: 0;
    visibility: hidden;
    .description{
        width: 80vw;
        min-height: 80vh;
        height: 500px;
        max-height: 1000vh;
        position: fixed;
        top: 10%;
        left: 10%;
        background-color: rgb(235, 207, 166);
        border-radius: 20px;
        
    }
    .exit{
        width: 50px;
        position: absolute;
        top: 3%;
        right: 6%;
        cursor: pointer;
    }
    .main__top-block, .main__bottom-block{
        width: 80vw;
        margin-left: 20px;
        margin-right: 20px;
        display: flex;
    }
    
    .contact{
        margin-top: 20px;
        width: 250px;
        font-size: 40px;
        .contact__title-photo{
            max-width: 250px;
            max-height: 250px;
            border-radius: 20px    ;
        }

    }
    .Name{
        width: 250px ;
    }
    .mobile{
        display: flex;
        font-size: 20px;
        margin-top: 10px;
        img{
            width: 20px;
            height: 20px;
            margin-top: 7px;
            transform: rotate(20deg);
            margin-right: 7px;
        }
    }
    .email{
        display: flex;
        font-size: 20px;
        img{
            width: 20px;
            height: 20px;
            margin-right: 7px   ;
        }
    }
    .exp-job{
        margin-left: 50px;
        margin-top: 20px;
        font-size: 20px;
        margin-right: 120px;
    }
    .soft-skills__title{
        text-align: center;
        margin-top: 20px;
    }
    .soft-skills{
        margin-right: 20px  ;
        ul{
            list-style: none;
        }
        span{
            font-weight: bold;
        }
    }
}
.active{
    visibility: visible;
}
.remove{
    visibility: hidden;
}
@media (max-width: 734px){
    
    .main__top-block{
        margin-left: 0 !important;
        display: block !important;
        .exp-job{
            margin-right: 40px  ;
            text-align: left;
        }
    }
    .shadow-block{
        text-align: center;
    }
    .contact{
        margin: 0 auto;
    }
    .contact__title-photo{
        width: 250px;
        height: 250px;
    }
    
}
@media (max-width: 1000px){
    .description{
        overflow-y: scroll;
        overflow-x: hidden;
    }
}
