.jobs{
    padding-top: 200px;
    max-width: 900px;
    padding-bottom: 150px;
    margin: 0 auto;
    gap:  50px  ;
    display: flex;
    .jobs_js-project, .jobs_website{
        width: 400px;
        height: 400px;
        margin: 0 auto;

        text-decoration: none;
        color: black;
        text-align: center;
        display: block;
        img{
            width: 100%;
            height: auto;
        }
    }
    .linkJsx{
        width: 100px;
    }
}
@media (max-width: 600px){
    .jobs{
        display: block;
    }
    .jobs_js-project, .jobs_website{
        max-width: 400px;
        max-height: 400px;
        margin-bottom: 50px !important;
    }
}
@media (max-width: 500px){
    .jobs{
        display: block;
    }
    .jobs_js-project, .jobs_website{
        max-width: 300px;
        max-height: 300px;
        margin-bottom: 50px !important;
        max-height: 242px !important;
    }
}
@media (max-width: 400px){
    .jobs_js-project, .jobs_website{
        max-width: 75.625vw;
        max-height: 30.644vh;
    }
}
